:root {
  --f-body: "Montserrat", sans-serif;
  --c-primary: #292929;
  --c-secondary: #da002e;
  --c-heading: #000000;
  --c-text: #4A4B4C;
  --c--background: #F5F6F7;
}

h1, .h1,
h2, .h2,
h3, .h3, .site-content .post h2, .blog-grid .post__title h2,
h4, .h4, .site-content .post h3,
h5, .h5, .site-content .post h4,
h6, .h6, .site-content .post h5 {
  font-family: var(--f-body);
  color: black;
  line-height: 1.3;
}

h1, .h1 {
  font-size: 2.7rem;
}
@media (min-width: 768px) {
  h1, .h1 {
    font-size: 3.4rem;
  }
}
@media (min-width: 992px) {
  h1, .h1 {
    font-size: 4rem;
  }
}
@media (min-width: 1420px) {
  h1, .h1 {
    font-size: 5rem;
  }
}

h2, .h2 {
  font-size: 2.3rem;
  margin-bottom: 2rem;
}
@media (min-width: 992px) {
  h2, .h2 {
    font-size: 3.4rem;
    margin-bottom: 3rem;
  }
}

h3, .h3, .site-content .post h2, .blog-grid .post__title h2 {
  font-size: 1.8rem;
  margin-bottom: 1.7rem;
  margin-top: 2rem;
}
@media (min-width: 992px) {
  h3, .h3, .site-content .post h2, .blog-grid .post__title h2 {
    font-size: 2.3rem;
    margin-bottom: 2rem;
    margin-top: 2.6rem;
  }
}

h4, .h4, .site-content .post h3 {
  line-height: 1.45;
}

p {
  margin-bottom: 1rem;
}

a {
  display: inline-block;
  position: relative;
  color: black;
  background: linear-gradient(0deg, var(--c-secondary) 0%, var(--c-secondary) 25%, rgba(0, 0, 0, 0) 25%);
  transition: all 0.3s ease-in-out;
}
a:hover {
  background-color: var(--c-secondary);
}

.read-more > svg {
  display: inline-block;
  width: 18px;
  height: 18px;
  overflow: hidden;
  transform: translateY(3px);
  margin-left: 3px;
}
.read-more:hover svg > path {
  stroke: white !important;
}

.label-uppercase {
  letter-spacing: 1px;
  font-family: var(--f-body);
  text-transform: uppercase;
  font-size: 1rem;
}

.list-inline {
  list-style: none;
}
.list-inline > li {
  display: inline-block;
}

.strikethrough {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  position: absolute;
  left: 0;
  bottom: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

svg {
  display: inline-block;
}

body {
  background-color: white;
  line-height: 1.6;
  font-family: var(--f-body);
  font-weight: 400;
  font-size: 17px;
  color: #4A4B4C;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
}

.container {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .container {
    max-width: 100% !important;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 980px !important;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px !important;
  }
}
@media (min-width: 1420px) {
  .container {
    max-width: 1360px !important;
  }
}
@media (min-width: 1640px) {
  .container {
    max-width: 1500px !important;
  }
}

.site-main,
.site-main > .post {
  margin-top: 150px;
}
.site-main > *,
.site-main > .post > * {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
  margin-bottom: 1.5rem;
}
.site-main > *:not(.alignwide):not(.alignfull):not(.post),
.site-main > .post > *:not(.alignwide):not(.alignfull):not(.post) {
  max-width: 100%;
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 992px) {
  .site-main > *:not(.alignwide):not(.alignfull):not(.post),
.site-main > .post > *:not(.alignwide):not(.alignfull):not(.post) {
    max-width: 980px;
  }
}
.site-main > *:not(.alignfull) .container,
.site-main > .post > *:not(.alignfull) .container {
  padding-left: 0;
  padding-right: 0;
}
.site-main > *.alignwide,
.site-main > .post > *.alignwide {
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 768px) {
  .site-main > *.alignwide,
.site-main > .post > *.alignwide {
    max-width: 100% !important;
  }
}
@media (min-width: 992px) {
  .site-main > *.alignwide,
.site-main > .post > *.alignwide {
    max-width: 980px !important;
  }
}
@media (min-width: 1200px) {
  .site-main > *.alignwide,
.site-main > .post > *.alignwide {
    max-width: 1140px !important;
  }
}
@media (min-width: 1420px) {
  .site-main > *.alignwide,
.site-main > .post > *.alignwide {
    max-width: 1360px !important;
  }
}
@media (min-width: 1640px) {
  .site-main > *.alignwide,
.site-main > .post > *.alignwide {
    max-width: 1500px !important;
  }
}
.site-main > *.alignfull,
.site-main > .post > *.alignfull {
  max-width: 100%;
}
.site-main > ul, .site-main > ol,
.site-main > .post > ul,
.site-main > .post > ol {
  padding-left: 40px !important;
}

button,
input[type=submit],
.button,
.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: black;
  color: white;
  padding: 10px 20px;
  font-weight: 700;
  text-align: center;
  border: solid 2px transparent;
  font-size: 17px;
}
@media (min-width: 768px) {
  button,
input[type=submit],
.button,
.btn {
    font-size: 19px;
  }
}
button.btn-secondary,
input[type=submit].btn-secondary,
.button.btn-secondary,
.btn.btn-secondary {
  color: black;
  background-color: var(--c-secondary);
}
button:hover,
input[type=submit]:hover,
.button:hover,
.btn:hover {
  color: white;
}
button:hover svg > path,
input[type=submit]:hover svg > path,
.button:hover svg > path,
.btn:hover svg > path {
  stroke: white !important;
}

.btn-text, .read-more {
  font-size: 15px;
  display: inline-block;
  white-space: nowrap;
  position: relative;
  padding: 0 6px;
  line-height: 28px;
  color: black;
  font-family: var(--f-body);
  background: none;
  border: none;
  transition: background-color 0.3s ease-in-out;
}
@media (min-width: 992px) {
  .btn-text, .read-more {
    font-size: inherit;
    line-height: 32px;
  }
}
.btn-text:hover, .read-more:hover {
  color: white;
  background-color: var(--c-secondary);
  transition: background-color 0.3s ease-in-out;
}

input[type=text],
input[type=password],
input[type=email],
input[type=search],
input[type=tel] {
  background: rgba(0, 0, 0, 0.04);
  color: black;
  padding: 10px;
  transition: background 0.3s ease-in-out;
  width: 100%;
  max-width: unset;
  border: none;
}
input[type=text]:focus, input[type=text]:active,
input[type=password]:focus,
input[type=password]:active,
input[type=email]:focus,
input[type=email]:active,
input[type=search]:focus,
input[type=search]:active,
input[type=tel]:focus,
input[type=tel]:active {
  background: var(--c-secondary);
}

textarea {
  background: rgba(0, 0, 0, 0.04);
  color: black;
  padding: 10px;
  transition: background 0.3s ease-in-out;
  width: 100%;
  font-family: inherit;
}
textarea:focus, textarea:active {
  background: var(--c-secondary);
}

label {
  color: black;
  font-weight: 700;
  cursor: pointer;
}

.form-row {
  display: inline-flex;
  width: 100%;
  margin-bottom: 1em;
  position: relative;
  flex-flow: column nowrap;
}
@media (min-width: 992px) {
  .form-row {
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
  }
}
.form-row > label {
  position: relative !important;
  flex: 0 0 100%;
  margin-bottom: 0.4rem;
  font-size: 15px;
}
@media (min-width: 992px) {
  .form-row > label {
    flex: 0 1 300px;
    width: 300px;
    margin-bottom: unset;
  }
}
.form-row > .woocommerce-input-wrapper {
  flex-grow: 1;
  width: 100%;
  position: relative;
}

.site-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  background-color: rgba(255, 255, 255, 0.925);
  padding: 15px;
  z-index: 900;
  backdrop-filter: blur(30px);
  -webkit-backdrop-filter: blur(30px);
  border-bottom: solid 1px rgba(0, 0, 0, 0.09);
  transition: transform 0.5s ease, box-shadow 0.4s ease-in-out;
}
.site-header .menu > ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  font-weight: 700;
  font-size: 1.2rem;
}
.site-header .menu > ul > li {
  position: relative;
  padding-left: 4px;
  padding-right: 4px;
}
.site-header .menu > ul > li:not(:last-child) {
  margin-right: 20px;
}
.site-header .menu > ul > li.current_page_item:before {
  content: "";
  display: block;
  width: 100%;
  height: 12px;
  position: absolute;
  bottom: 0;
  left: 0;
  background: var(--c-secondary);
}
.site-header .menu > ul > li > a {
  color: black;
  background: none;
}
.site-header__actions {
  display: flex;
  flex-flow: row wrap;
  align-self: flex-end;
  margin-left: auto;
}
.site-header__actions .btn-action {
  background: white;
  border: none;
  padding: 5px;
  transition: all 0.3s ease-in-out;
}
.site-header__actions .btn-action svg {
  width: 30px;
  height: 30px;
}
.site-header__actions .btn-action #count-products-cart {
  color: black;
  font-weight: 500;
  line-height: 1;
  position: relative;
  margin-left: 10px;
}
.site-header__actions .btn-action #count-products-cart:not(.is-visible) {
  display: none;
}
.site-header__actions .btn-action #count-products-cart:before {
  content: "";
  display: block;
  width: 1px;
  height: 15px;
  background-color: rgba(0, 0, 0, 0.09);
  position: absolute;
  top: 2px;
  left: -5px;
}
.site-header__actions .btn-action:not(:last-child) {
  margin-right: 5px;
}
.site-header__actions .btn-action:hover {
  background-color: var(--c-secondary);
}
.site-header.is-scrolled {
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
  border-bottom-color: transparent;
}
.site-header.is-hidden {
  transform: translateY(-80px);
}

.admin-bar .site-header {
  top: 32px;
}

.select2-container--default .select2-selection--single {
  background: rgba(0, 0, 0, 0.04);
  border: none;
  border-radius: 0px;
  padding: 10px;
  height: unset;
  width: 100%;
}
